import * as CONST_TEMPERATURE from "constants/tools/temperature";
import { round, toNumber } from "helpers/number";
import { T_ConversionFormulas } from "types/tools/unitConverter";

export const conversionFormulas: T_ConversionFormulas = {
  baseUnitCode: CONST_TEMPERATURE.baseUnit,
  fromBaseTo: {
    K: (K: Kelvin) => toNumber(K),
    C: (K: Kelvin) => round(toNumber(K) - 273.15),
    F: (K: Kelvin) => round(((toNumber(K) - 273.15) * 9) / 5) + 32,
    R: (K: Kelvin) => round((toNumber(K) * 9) / 5)
  },
  toBaseFrom: {
    K: (K: Kelvin) => toNumber(K),
    C: (C: Celcius) => round(toNumber(C) + 273.15),
    F: (F: Fahrenheit) => round(((toNumber(F) - 32) * 5) / 9) + 273.15,
    R: (R: Kelvin) => round((toNumber(R) * 5) / 9)
  }
};

type Kelvin = number;
type Fahrenheit = number;
type Celcius = number;
